/* eslint-disable new-cap */
import { h, Component } from 'preact';
import Renderer from 'js13k-2d';
import style from './style';

const { Point, Texture, Frame, Sprite } = Renderer;

let tint = Math.random() * 0xffffff;

const atlasImg = () => {
	const canvas = document.createElement('canvas');
	const size = 32;
	const half = size / 2;
	canvas.width = size;
	canvas.height = size;
	const ctx = canvas.getContext('2d');

	let offset = 0;

	ctx.lineWidth = size / 16;
	ctx.fillStyle = '#cccccc';
	ctx.strokeStyle = '#000000';
	ctx.beginPath();

	ctx.moveTo(offset + half, half);
	for (let angle = 0; angle < Math.PI * 2; angle += (Math.PI * 2) / 5) {
		ctx.lineTo(
			offset + half - Math.sin(angle) * half * 0.9,
			half - Math.cos(angle) * half * 0.9
		);
	}

	ctx.closePath();
	ctx.fill();
	ctx.stroke();

	return canvas;
};

class Bunny extends Sprite {
	constructor(frame, props) {
		super(frame, props);

		this.gravity = 0.75;
		this.speedX = Math.random() * 4;
		this.speedY = Math.random() * 4 - 2;
	}

	update(bounds) {
		this.position.x += this.speedX;
		this.position.y += this.speedY;
		this.speedY += this.gravity;

		if (this.position.x > bounds.right) {
			this.speedX *= -1;
			this.position.x = bounds.right;
		}
		else if (this.position.x < bounds.left) {
			this.speedX *= -1;
			this.position.x = bounds.left;
		}

		if (this.position.y > bounds.bottom) {
			this.speedY *= -0.85;
			this.position.y = bounds.bottom;
			if (Math.random() > 0.5) {
				this.speedY -= Math.random() * 6;
			}
		}
		else if (this.position.y < bounds.top) {
			this.speedY = 0;
			this.position.y = bounds.top;
		}
	}
}

export default class Bunnymark extends Component {
	onmousedown = event => {
		this.add = true;
		tint = Math.random() * 0xffffff;
		event.preventDefault();
	};

	ontouchstart = event => {
		this.add = true;
		tint = Math.random() * 0xffffff;
		event.preventDefault();
	};

	onmouseup = event => {
		this.add = false;
		event.preventDefault();
	};

	ontouchend = event => {
		this.add = false;
		event.preventDefault();
	};

	componentDidMount() {
		const view = document.getElementById('view');

		this.add = false;

		const scale = window.devicePixelRatio === 1 ? 1 : 2;
		const scene = Renderer(view, { scale });
		const { gl } = scene;

		scene.background(1, 1, 1);

		const atlas = Texture(scene, atlasImg(), 0.5, {
			[gl.TEXTURE_MAG_FILTER]: gl.LINEAR,
			[gl.TEXTURE_MIN_FILTER]: gl.LINEAR
		});
		atlas.anchor = Point(0.5);

		const frames = [Frame(atlas, Point(), Point(32))];

		let len = 0;
		let cl = 0;
		const sprs = [];

		const addSprite = a => {
			if (len % 250 === 0) {
				cl++;
			}
			len += a;

			const layer = scene.layer(cl);

			const { width } = view;

			for (let i = 0; i < a; i++) {
				const sprite = new Bunny(frames[i % frames.length], {
					position: Point((i % 2) * width, 0),
					// scale: Point(0.5),
					tint
				});

				layer.add(sprite);
				sprs.push(sprite);
			}
		};

		const loop = delta => {
			scene.resize();

			if (len < 3000 || this.add) addSprite(25);

			const bounds = {
				left: 0,
				top: 0,
				right: view.width,
				bottom: view.height
			};

			sprs.forEach(sprite => sprite.update(bounds));

			scene.render();

			return [
				{
					title: 'sprites',
					value:
						len < 10000
							? len
							: `${Number((len / 1000).toFixed(1))}k`
				}
			];
		};

		this.props.start(loop);
	}

	/*
                <h1>Home</h1>
                <p>This is the Home component.</p>
    */

	render() {
		return (
			<div
				class={style.home}
				onMouseDown={this.onmousedown}
				onTouchStart={this.ontouchstart}
				onMouseUp={this.onmouseup}
				onTouchEnd={this.ontouchend}
			>
				Click or touch to add sprites
				<canvas id="view" />
			</div>
		);
	}
}



