/* eslint-disable new-cap */
import { h, Component } from 'preact';
import Renderer from 'js13k-2d';
import style from './style';

const { Point, Texture, Frame, Sprite } = Renderer;

/*
const sprites = document.getElementById('info');

const dbgRenderInfo = gl.getExtension('WEBGL_debug_renderer_info');
const info = gl.getParameter(
    dbgRenderInfo ? dbgRenderInfo.UNMASKED_RENDERER_WEBGL : gl.VENDOR
);
*/

const atlasImg = () => {
	const canvas = document.createElement('canvas');
	const size = 32;
	const half = size / 2;
	canvas.width = 96;
	canvas.height = 32;
	const ctx = canvas.getContext('2d');

	let offset = 0;

	ctx.lineWidth = size / 16;
	ctx.fillStyle = '#cccccc';
	ctx.strokeStyle = '#000000';
	ctx.beginPath();

	ctx.moveTo(offset + half, half);
	for (let angle = 0; angle < Math.PI * 2; angle += (Math.PI * 2) / 5) {
		ctx.lineTo(
			offset + half - Math.sin(angle) * half * 0.9,
			half - Math.cos(angle) * half * 0.9
		);
	}

	ctx.closePath();
	ctx.fill();
	ctx.stroke();

	offset += size;

	ctx.beginPath();

	ctx.moveTo(offset + 3, 3);
	ctx.lineTo(offset + size - 3, 3);
	ctx.lineTo(offset + size - 3, size - 3);
	ctx.lineTo(offset + 3, size - 3);

	ctx.closePath();
	ctx.fill();
	ctx.stroke();

	offset += size;

	ctx.beginPath();

	ctx.moveTo(offset + 3, 3);
	ctx.lineTo(offset + 29, 3);
	ctx.lineTo(offset + 29, 8);
	ctx.lineTo(offset + 8, 8);
	ctx.lineTo(offset + 8, 14);
	ctx.lineTo(offset + 20, 14);
	ctx.lineTo(offset + 20, 18);
	ctx.lineTo(offset + 8, 18);
	ctx.lineTo(offset + 8, 29);
	ctx.lineTo(offset + 3, 29);

	ctx.closePath();
	ctx.fill();
	ctx.stroke();

	return canvas;
};

export default class Home extends Component {
	onmousedown = event => {
		this.add = true;
		event.preventDefault();
	};

	ontouchstart = event => {
		this.add = true;
		event.preventDefault();
	};

	onmouseup = event => {
		this.add = false;
		event.preventDefault();
	};

	ontouchend = event => {
		this.add = false;
		event.preventDefault();
	};

	componentDidMount() {
		const view = document.getElementById('view');

		this.add = false;

		/*
        let add = false;
        view.onmousedown = event => {
            event.preventDefault();
            add = true;
        };
        view.ontouchstart = event => {
            add = true;
            event.preventDefault();
        };
        view.onmouseup = event => {
            add = false;
            event.preventDefault();
        };
        view.ontouchend = event => {
            add = false;
            event.preventDefault();
        };
        */

		const scale = window.devicePixelRatio === 1 ? 1 : 2;
		const scene = Renderer(view, { scale });
		const { gl } = scene;

		const maskSize = Math.min(
			scene.gl.canvas.width,
			scene.gl.canvas.height
		);

		scene.background(1, 1, 1, 0);
		scene.camera.at.set(maskSize / 2, maskSize / 2);
		scene.camera.to.set(0.5);

		const logoMask = () => {
			const canvas = document.createElement('canvas');
			canvas.width = maskSize;
			canvas.height = maskSize;
			const ctx = canvas.getContext('2d');

			ctx.fillStyle = '#ffffff';
			ctx.beginPath();

			ctx.moveTo(maskSize / 2, maskSize / 2);
			for (
				let angle = 0;
				angle < Math.PI * 2;
				angle += (Math.PI * 2) / 5
			) {
				ctx.lineTo(
					maskSize / 2 - (Math.sin(angle) * maskSize) / 2.5,
					maskSize / 2 - (Math.cos(angle) * maskSize) / 2.5
				);
			}

			ctx.closePath();
			ctx.fill();

			const { data } = ctx.getImageData(0, 0, maskSize, maskSize);

			return (x, y) => data[(y * maskSize + x) * 4] > 0;
		};

		/*
        const logo = () => {
            const canvas = document.createElement('canvas');
            canvas.width = 64;
            canvas.height = 64;
            const ctx = canvas.getContext('2d');

            ctx.lineWidth = 0;
            ctx.strokeStyle = '#3066a9';
            ctx.fillStyle = '#3066a9';
            ctx.beginPath();

            ctx.moveTo(32, 32);
            for (
                let angle = 0;
                angle < Math.PI * 2;
                angle += (Math.PI * 2) / 5
            ) {
                ctx.lineTo(
                    32 - Math.sin(angle) * 30,
                    32 - Math.cos(angle) * 30
                );
            }

            ctx.closePath();
            ctx.stroke();
            ctx.fill();

            const image = new Image();
            image.src = canvas.toDataURL('image/png');
            document.body.appendChild(image);
        };

        logo();
        */

		const atlas = Texture(scene, atlasImg(), 0.5, {
			[gl.TEXTURE_MAG_FILTER]: gl.LINEAR,
			[gl.TEXTURE_MIN_FILTER]: gl.LINEAR
		});
		atlas.anchor = Point(0.5);

		const bFrame = Frame(atlas, Point(), Point(32));
		const qFrame = Frame(atlas, Point(32, 0), Point(32));
		const fFrame = Frame(atlas, Point(64, 0), Point(32));
		const frames = [atlas, bFrame, qFrame, fFrame];

		const mask = logoMask();
		const sprs = [];

		let len = 0;
		let cl = 0;

		const addSprite = a => {
			if (len % 250 === 0) {
				cl++;
			}
			len += a;

			const layer = scene.layer(cl);

			for (let i = 0; i < a; i++) {
				const sprite = Sprite(frames[i % frames.length]);

				let x = 0;
				let y = 0;

				while (!mask(x, y)) {
					x = ~~(maskSize * Math.random());
					y = ~~(maskSize * Math.random());
				}

				sprite.position.set(x, y);
				sprite.tint = Math.random() * 0xffffff;
				sprite.rotation = Math.random() * Math.PI * 2;
				sprite.dr = (0.5 - Math.random()) * 0.1;
				sprite.trans = !Math.round(Math.random() + 0.3);
				sprs.push(sprite);
				layer.add(sprite);
			}
		};

		let s = 0;
		const loop = delta => {
			if (len < 3000 || this.add) addSprite(25);

			s += 0.005;
			const scale = ((Math.abs(Math.cos(s)) + 0.1) * maskSize) / 1300;

			sprs.forEach(sprite => {
				sprite.scale.set(scale);
				sprite.rotation += sprite.dr;
				if (sprite.trans) {
					if (sprite.alpha > 0.6) {
						sprite.alpha -= delta / 10000;
					}
					else {
						sprite.trans = false;
					}
				}
			});

			scene.camera.angle += delta / 3000;

			scene.render();

			return [
				{
					title: 'sprites',
					value:
						len < 10000
							? len
							: `${Number((len / 1000).toFixed(1))}k`
				}
			];
		};

		this.props.start(loop);
	}

	/*
                <h1>Home</h1>
                <p>This is the Home component.</p>
    */

	render() {
		return (
			<div
				class={style.home}
				onMouseDown={this.onmousedown}
				onTouchStart={this.ontouchstart}
				onMouseUp={this.onmouseup}
				onTouchEnd={this.ontouchend}
			>
				Click or touch to add sprites
				<canvas id="view" />
			</div>
		);
	}
}



